import { Renderer } from "meiosis";
import { meiosisRender, MeiosisRender, RenderIntoElement } from "meiosis-render";

function renderIntoElement(element: Element): Renderer<string> {
  return function(view: string): void {
    element.innerHTML = view;
  };
}

export interface MeiosisVanillaJs extends MeiosisRender<string> {
  on(target: Element, type: string, handler: EventListener, useCapture: boolean): void;
  delegate(target: Element, selector: string, type: string, handler: EventListener): void;
}

function on(target: Element, type: string, handler: EventListener, useCapture: boolean): void {
  target.addEventListener(type, handler, !!useCapture);
}

function dispatchEvent(target: Element, selector: string, handler: EventListener): EventListener {
  return function(evt: Event) {
    const targetElement = evt.target;
    const potentialElements = target.querySelectorAll(selector);
    const hasMatch = Array.prototype.indexOf.call(potentialElements, targetElement) >= 0;

    if (hasMatch) {
      handler.call(targetElement, evt);
    }
  };
};

function delegate(target: Element, selector: string, type: string, handler: EventListener): void {
  // https://developer.mozilla.org/en-US/docs/Web/Events/blur
  on(target, type, dispatchEvent(target, selector, handler), type === "blur" || type === "focus");
}

const meiosisVanillaJsRender: MeiosisRender<string> = meiosisRender(renderIntoElement);

const renderer: MeiosisVanillaJs = {
  renderIntoElement: meiosisVanillaJsRender.renderIntoElement,
  renderIntoId: meiosisVanillaJsRender.renderIntoId,
  renderIntoSelector: meiosisVanillaJsRender.renderIntoSelector,
  intoElement: meiosisVanillaJsRender.intoElement,
  intoId: meiosisVanillaJsRender.intoId,
  intoSelector: meiosisVanillaJsRender.intoSelector,
  on,
  delegate
};

export { renderer };



